@font-face {
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 200;
  src: url("../assets/fonts/AvenirNext-Regular.woff") format("woff");
}

@font-face {
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 400;
  src: url("../assets/fonts/AvenirNext-Medium.woff") format("woff");
}

@font-face {
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 600;
  src: url("../assets/fonts/AvenirNext-DemiBold.woff") format("woff");
}

@font-face {
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 700;
  src: url("../assets/fonts/AvenirNext-Bold.woff") format("woff");
}

html,
body {
  font-family: "Avenir Next", sans-serif;
}

h1, h2, h3, h4 {
  color: #0c0b61;
}

a {
  color: #036eff;
}

.btn {
  display: inline-block;
  padding: 0.625rem 2.5rem;
  border-radius: 999px;
  font-size: 0.75rem;
  font-weight: 600;
  background: none;
  color: #036eff;
  border: none;
  box-shadow: inset 0 0 0 2px #036eff;
  transition: all .2s ease-in-out;
}

.btn:active, .btn:focus {
  outline: none;
}

.btn:hover {
  cursor: pointer;
  background-color: #036eff;
  color: white;
}

.spinner {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.spinner:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #036eff;
  border-color: #036eff transparent #036eff transparent;
  animation: spinner 1.2s linear infinite;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.site-header {
  background-color: #fff;
  border-bottom: solid 1px #eaeaea;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 25px 15px;
}

@media (min-width: 992px) {
  .container {
    padding: 25px 15%;
  }
}

.branding .branding-logo {
  height: 50px;
  float: left;
  margin-right: 25px;
  font-size: 16px;
}

.branding .branding-title {
  font-family: "Avenir Next", sans-serif;
  font-weight: 800;
  line-height: 58px;
  color: #091a52;
  text-transform: uppercase;
}

.main-left,
.main-right {
  padding: 0 2.5%;
}

@media (min-width: 992px) {
  .main-left,
  .main-right {
    float: left;
    padding: 0;
    width: 45%;
  }
}

@media (min-width: 992px) {
  .main-left {
    padding-right: 5%;
    width: 45%;
  }
}

.incident-form {
  background: rgba(238, 238, 238, 0.5);
  padding: 40px;
}

.incident-form label,
.incident-form input {
  margin: 25px 0;
}

.incident-form input[type="text"] {
  padding: 5px 5px;
  border: 1px solid #ddd;
  font-size: 1.125rem;
  color: #000;
  width: 100%;
}

.incident-form input[type="text"], .incident-form input[type="text"]::placeholder {
  font-size: 15px;
}

.incident-form .message {
  margin: -10px 0 25px;
}
